.navbar {
  height: 50px;
  width: 100%;
  background-color: #ef8029;
  top: 0;
  left: 0;
  align-items: center;
  color: #fff;
  margin-top: -8px;
  margin-left: 8px;
}
.navbar-expand {
  flex-flow: row-reverse !important;
}
.sidebar_items {
  padding: inherit;
}
.sidebar {
  left: 0;
  position: absolute;
  width: 250px;
  background-color: #101827;
  justify-content: space-between;
  top: 0;
}
.nav-text {
  color: #fff;
  height: 45px;
  list-style: none;
  justify-content: start;
  align-items: center;
  padding: 0px 0px 0px 16px;
  font-family: MONTSERRAT-LIGHT !important;
}
.nav-text a {
  text-decoration: none;
  color: rgb(230, 221, 221);
  font-size: 16px;
  padding: 0 16px;
  display: flex;
  border-radius: 4px;
}
.nav-text a:active,
nav-text a:focus {
  font-weight: 600;
}
.nav-text a:hover {
  color: #ef8029;
}
.nav-items {
  margin-left: 15px;
}
.title {
  color: white !important;
  margin-left: 62px !important;
  margin-top: 42px;
  font-size: 24px;
  font-weight: 600;
}
.dash:hover {
  fill: #ef8029;
}
.figure {
  position: relative;
  display: inline-block;
  width: 145px;
  height: auto;
  margin-left: 32px;
  margin-top: 20px;
}
.figure img.image-hover {
  display: none;
  position: absolute;
  opacity: 0;
  transition: opacity 0.2s;
  width: 145px;
  height: auto;
  margin-left: 32px;
}
.figure:hover img.image-hover {
  opacity: 1;
  display: inline;
}
.image-main {
  width: 145px;
  height: auto;
  margin-left: 3px;
}
.jaitra-logo {
  font-size: 14px;
  color: #fff;
  margin-left: 56px;
  margin-top: 15rem;
}
.nav-icon {
  cursor: pointer;
  height: 1.5rem;
  width: 1.5rem;
  margin-left: 1212px;
  margin-top: -18px;
  margin-bottom: 9px;
}
.figure:hover {
  fill: #ef8029;
}
.nav-text a.active {
  font-weight: bold;
  color: #ef8029;
}
.arca-user {
  color: white;
  margin-top: -16px;
  margin-bottom: -19px;
  margin-left: 1142px;
}
.MuiToolbar-regular {
  justify-content: flex-end;
}
